import layout from "./layout/AppLayout";
import apiFolder from "../../service/axios/folder";
import modalTask from "../components/modal/AddOrModTask.vue";
import userApi from "../../service/axios/user";
import level from "../../service/axios/level";
import snackbar from "../components/SnackBar.vue";
import taskApi from "../../service/axios/task";
import modalDelete from "../components/modal/DeleteModal.vue";
import versionApi from "../../service/axios/version";

export default {
    data() {
        return {
            archivedVisible: false,
            overlay: false,
            folders: [],
            foldersSave: [],
            btnFilterUser: false,
            btnFilterLevel: false,
            search: "",
            searchUser: "",
            searchLevels: "",
            nothingView: true,
            loading: true,
            users: [],
            levels: [],
            textSnack: "",
            folderDeleteName: "",
            boolSnack: false,
            colorSnack: "primary",
            listFolder: [],
            dialogDelete: true,
            initialise: false,
            version: 0.0,
        };
    },

    computed: {
        filterUsers() {
            var res = this.users.filter((user) => {
                return user.lastName
                    .toLowerCase()
                    .includes(this.searchUser.toLowerCase());
            });
            return res;
        },
        filterLevels() {
            var res = this.levels.filter((level) => {
                return level.label
                    .toLowerCase()
                    .includes(this.searchLevels.toLowerCase());
            });
            return res;
        },
        filterFolders() {
            var res = this.folders.filter((folder) => {
                return folder.label.toLowerCase().includes(this.search.toLowerCase());
            });

            if (res.length > 0) {
                this.nothingView = false;
            } else {
                this.nothingView = true;
            }

            return res;
        },
    },
    mounted() {
        this.refresh();
    },
    created() {
        this.initialize();
    },
    methods: {
        getTaskarchived(task) {
            if (task.archived) {
                if (!this.archivedVisible) {
                    return false;
                }
            }

            return true;
        },
        activeOver(value) {
            this.overlay = value;
        },
        refresh() {
            setInterval(this.checkVersionBdd, 10000);
        },

        async checkVersionBdd() {
            var res = await versionApi.index(this.version);
            console.log(res);
            if (res.data.modify) {
                this.version = res.data.version;
                this.initialize();
            }
        },
        deleteTask(index) {
            this.textSnack = "la tache a été supprimé";
            this.colorSnack = "success";
            this.boolSnack = true;
            this.folders[index.iFolder].Tasks.splice(index.iTask, 1);
        },
        async deleteFolder(index) {
            this.textSnack = "votre dossier a été supprimé";
            this.colorSnack = "success";
            this.boolSnack = true;
            var result = await apiFolder.listFolder();
            this.listFolder = result.data;
            this.folders.splice(index, 1);
        },
        async archiveFolder(folder) {
            if (
                confirm(`Voulez-vous vraiment archiver le dossier ${folder.label} ?`)
            ) {
                this.overlay = true;
                this.textSnack = "votre dossier a été archivé";
                this.colorSnack = "success";
                this.boolSnack = true;
                folder.Tasks.forEach((task) => {
                    this.archiveTask(task, false);
                });

                var res = await apiFolder.archived(folder.id);
                folder.archived = true;
                this.overlay = false;
            }
        },
        async archiveTask(task, snack = true) {
            if (confirm(`Voulez-vous vraiment archiver la tache ${task.title} ?`)) {
                this.overlay = true;
                var res = await taskApi.archived(task.id);
                if (snack) {
                    this.textSnack = "votre tache a été archivé";
                    this.colorSnack = "success";
                    this.boolSnack = true;
                }

                task.archived = true;
                this.overlay = false;
            }
        },
        addTask(task) {
            this.textSnack = "votre tache a été ajouté";
            this.colorSnack = "success";
            this.boolSnack = true;

            if (task.newFolder) {
                this.initialize();
            } else {
                this.folders.forEach((folder, i) => {
                    if (folder.id == task.folder) {
                        this.folders[i].Tasks.push(task);
                    }
                });
            }
        },
        updateTask(task) {
            this.textSnack = "votre tache a été modifié";
            this.colorSnack = "success";
            this.boolSnack = true;
            this.folders.forEach((folder, index) => {
                if (folder.id == task.folder) {
                    folder.Tasks.forEach((taskx, i) => {
                        if (taskx.id == task.id) {
                            this.folders[index].Tasks[i] = task;
                        }
                    });
                }
            });
        },
        filterListTask(tasks) {
            var res = tasks.filter((task) => {
                return task.level.label.includes(this.searchLevels);
            });

            var data = res.filter((task) => {
                var result = task.Users.filter((user) => {
                    return user.user.lastName.includes(this.searchUser);
                });
                if (result.length > 0) {
                    return true;
                }
                return false;
            });

            return data;
        },

        deleteFilterUser() {
            this.searchUser = "";
        },

        deleteFilterLevel() {
            this.searchLevels = "";
        },
        async initialize() {
            if (!this.initialise) {
                var users = await userApi.index();
                this.users = users.data;
                console.log(users);
                var levels = await level.index();
                this.levels = levels.data;
                this.initialise = true;
            }
            var result = await apiFolder.listFolder();
            this.listFolder = result.data;
            var res = await apiFolder.index();
            console.log(res);
            if (res.data.length > 0) {
                this.loading = false;
            }
            this.folders = res.data;
        },
        getColWith(width) {
            if (width > 1400) {
                return "col-2";
            }
            if (width > 800 && width < 1400) {
                return "col-5";
            }
            if (width < 800) {
                return "col-10";
            }
        },
        getRowWith(width) {
            if (width < 1400) {
                return "d-flex flex-column align-center";
            }
        },
        getListWith(width) {
            if (width > 850) {
                return "d-flex flex-row mb1";
            }
        },
    },
    name: "Home",
    components: {
        layout,
        modalTask,
        snackbar,
        modalDelete,
    },
};