import api from "./api";

//collection of request to API for computer
export default {
    index() {
        return api.get("levels");
    },
    store(level) {
        return api.post("level/store", level);
    },
    update(level) {
        return api.post("level/update", level);
    },
    delete(id) {
        return api.get(`level/${id}/delete`);
    },
};