export default {
    name: "snackbar",
    props: {
        snackOpen: Boolean,
        snackColor: String,
        textSnack: String,
    },

    watch: {
        snackOpen: function() {
            if (this.snackOpen) {
                this.snackbar = this.snackOpen;
                this.$emit("snackClose", false);
            }
        },
    },

    data() {
        return {
            snackbar: false,
            timeout: 2000,
        };
    },
};