import api from "./api";

//collection of request to API for computer
export default {
    store(data) {
        return api.post("task/add", data);
    },
    update(data) {
        return api.put("task/update", data);
    },
    archived(id) {
        return api.get(`task/${id}/archived`);
    },
    delete(id) {
        return api.get(`task/${id}/delete`);
    },
};