<template >
  <v-container style="background: #04153b" fill-height fluid>
    <v-row justify="center">
      <v-col class="col-12" align="center">
        <v-img src="../assets/logo.png" height="200px" width="200"></v-img>
      </v-col>
      <v-col
        style="border-bottom: 8px solid #a09f9f"
        xl="4"
        md="4"
        sm="2"
        cols="1"
      >
      </v-col>
      <v-col align="center" style="padding: 0" xl="4" md="4" sm="8" cols="10">
        <v-card color="secondary" style="border-radius: 50px 0px">
          <v-card-title class="white--text text-h4 justify-center">
            Login
          </v-card-title>
          <v-form
            class="white--text"
            ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-card-text>
              <v-text-field
                v-model="email"
                solo
                :rules="emailRules"
                label="Identifiant"
                required
              ></v-text-field>
              <v-text-field
                v-model="password"
                :append-icon="show4 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passwordRules"
                :type="show4 ? 'text' : 'password'"
                label="Password"
                solo
                error
                @click:append="show4 = !show4"
              ></v-text-field>
            </v-card-text>

            <v-card-actions class="justify-center">
              <v-btn :disabled="!valid" color="primary" @click="login">
                Valider
              </v-btn>
              <v-btn color="primary"> Reset password </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
      <v-col
        xl="4"
        md="4"
        sm="2"
        cols="1"
        style="border-top: 8px solid #a09f9f"
      >
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import userApi from "../../service/axios/user";
export default {
  data() {
    return {
      email: "",
      valid: true,
      show4: false,
      emailRules: [
        (v) => !!v || "Merci de saisir un email",
        (v) => v.length >= 3 || "Merci de saisir min 3 caractères",
        (v) => v.length <= 50 || "Max 25 characters",
      ],
      password: "",
      passwordRules: [
        (v) => !!v || "Merci de saisir votre mot de passe",
        (v) => v.length >= 8 || "Merci de saisir min 8 caractères",
        (v) => v.length <= 25 || "Max 25 characters",
      ],
    };
  },
  created() {
    if (localStorage.getItem("token")) {
      this.home();
    }
  },
  methods: {
    async home() {
      try {
        await userApi.userAuth();
        this.$router.push({ name: "Home" });
      } catch (error) {
        console.log(error);
      }
    },
    async login() {
      if (this.$refs.form.validate()) {
        var dataLogin = {
          email: this.email,
          password: this.password,
        };
        var res = await userApi.login(dataLogin);
        console.log(res);
        if (res.data.login) {
          localStorage.setItem("token", res.data.token);
          location.reload();
        }
      }
    },
  },
};
</script>

