<template>
  <v-container style="padding: 0" fluid>
    <v-app-bar color="primary" flat app>
      <template class="">
        <v-container
          class="d-flex justify-center col-lg-10 col-md-10 col-sm-12 col-xs-12"
          style="padding: 0"
        >
          <v-app-bar color="primary" flat>
            <v-btn color="primary" icon class="ml-2">
              <v-img
                src="../../assets/logo.png"
                max-height="90"
                max-width="90"
              ></v-img>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn v-if="width > 700" class="white--text" text>
              {{ user.lastName }} {{ user.firstName }}
            </v-btn>
            <v-btn class="white--text" icon><v-icon>mdi-account</v-icon></v-btn>
            <v-btn @click="logout()" class="red--text" icon>
              <v-icon>mdi-exit-to-app</v-icon>
            </v-btn>
          </v-app-bar>
        </v-container>
      </template>
    </v-app-bar>
    <v-tabs
      color="error"
      background-color="primary"
      vertical
      v-if="width <= 700"
    >
      <v-container
        class="col-lg-10 col-md-10 col-sm-12 col-xs-12"
        style="padding: 0"
      >
        <v-tab class="white--text" to="/">Home</v-tab>
        <v-tab class="white--text" to="/archive">Archive</v-tab>
        <v-tab class="white--text" to="/admin" v-if="user.admin">Admin</v-tab>
      </v-container>
    </v-tabs>
    <v-tabs
      color="error"
      background-color="primary"
      v-if="width > 700"
      align-with-title
    >
      <v-container
        class="col-lg-10 col-md-10 col-sm-12 col-xs-12 d-flex"
        style="padding: 0"
      >
        <v-tab class="white--text" to="/">Home</v-tab>
        <v-tab class="white--text" to="/archive">Archive</v-tab>
        <v-tab class="white--text" to="/admin" v-if="user.admin">Admin</v-tab>
      </v-container>
    </v-tabs>

    <v-container
      class="col-lg-10 col-md-10 col-sm-12 col-xs-12"
      style="padding: 0"
    >
      <slot :width="width" />
    </v-container>

    <v-footer padless fixed color="primary" class="white--text">
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} —
        <strong>MyTasks-v1.0.0</strong>
      </v-col>
    </v-footer>
  </v-container>
</template>

<script>
import userApi from "../../../service/axios/user";
export default {
  data() {
    return {
      width: document.documentElement.clientWidth,
      height: document.documentElement.clientHeight,
      user: {
        firstName: "",
        lastName: "",
        email: "",
        admin: false,
      },
    };
  },
  mounted() {
    window.addEventListener("resize", this.getDimensions);
    this.account();
  },
  unmounted() {
    window.removeEventListener("resize", this.getDimensions);
  },
  methods: {
    logout() {
      localStorage.removeItem("token");
      location.reload();
    },
    getDimensions() {
      this.width = document.documentElement.clientWidth;
      this.height = document.documentElement.clientHeight;
    },
    async account() {
      var res = await userApi.userAuth();

      this.user = res.data;
    },
  },
  name: "AppLayout",
};
</script>