import folderApi from "../../../service/axios/folder";
import taskApi from "../../../service/axios/task";
export default {
    name: "deleteModal",
    data() {
        return {
            dialogDelete: false,
            title: "",
        };
    },
    props: ["folderDel", "taskDel", "indexFolder", "indexTask"],

    created() {
        this.init();
    },

    methods: {
        async deleteItemConfirm() {
            if (this.taskDel != null) {
                var res = await taskApi.delete(this.taskDel.id);
                console.log(res);
                this.$emit("deleteTask", {
                    iTask: this.indexTask,
                    iFolder: this.indexFolder,
                });
            }
            if (this.folderDel != null) {
                var res = await folderApi.delete(this.folderDel.id);
                console.log(res);
                this.$emit("deleteFolder", this.indexFolder);
            }
            this.dialogDelete = false;
        },

        init() {
            if (this.taskDel != null) {
                this.title = this.taskDel.title;
            }
            if (this.folderDel != null) {
                this.title = this.folderDel.label;
            }
        },
    },
};