import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import Password from "../views/Reset.vue";
import userApi from "../../service/axios/user";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Home",
        component: Home,
        meta: {
            authRequired: true,
        },
    },
    {
        path: "/login",
        name: "Login",
        component: Login,
    },
    {
        path: "/user/:token/reset/password",
        name: "Password",
        component: Password,
    },
    {
        path: "/archive",
        name: "Archive",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/Archive.vue"),
        meta: {
            authRequired: true,
        },
    },
    {
        path: "/admin",
        name: "Admin",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/Admin.vue"),
        meta: { authRequired: true, admin: true },
    },
    { path: "*", redirect: "/login" },
];

const router = new VueRouter({
    routes,
});

router.beforeEach(async(to, from, next) => {
    if (to.matched.some((record) => record.meta.authRequired)) {
        if (localStorage.getItem("token") == null) {
            next({
                path: "/login",
            });
        } else {
            var token = localStorage.getItem("token");
            console.log(token);
            if (token.length > 15 && !token.includes("ey")) {
                next({
                    path: "/login",
                });
            }
            let user;

            try {
                var res = await userApi.userAuth();
                user = res.data;
                if (user.login) {
                    next();
                }
            } catch (error) {
                next({
                    path: "/login",
                });
            }

            if (to.matched.some((record) => record.meta.admin)) {
                if (user.admin) {
                    next();
                } else {
                    next({ name: "Home" });
                }
            } else {
                next();
            }
        }
    } else {
        next();
    }
});

export default router;