import api from "./api";
import axios from "axios";

//collection of request to API for computer
export default {
    index() {
        return api.get("users");
    },
    list() {
        return api.get("user/list");
    },
    store(user) {
        return api.post("user/store", user);
    },
    update(user) {
        return api.put("user/update", user);
    },
    delete(id) {
        return api.get(`user/${id}/delete`);
    },
    resetPassword(id) {
        return api.get(`user/${id}/reset/password`);
    },
    resetPasswordUser(password, token) {
        var temporary = axios.create({
            baseURL: `api`,
            headers: {
                Authorization: "Bearer " + token,
                "Content-Type": "application/json",
                Accept: "application/json",
            },
        });
        return temporary.post("user/reset/password", password);
    },
    login(data) {
        return api.post("user/login", data);
    },
    userAuth() {
        return api.get("user/auth");
    },
};