import taskApi from "../../../service/axios/task";

export default {
    methods: {},
    props: {
        taskDate: {
            default: null,
        },
        levels: null,
        listUsers: null,
        listFolders: null,
    },
    data() {
        return {
            dialog: false,
            overlay: false,
            addFolder: false,
            labelFolder: "",
            users: [],
            valid: true,
            titleTask: "",
            titleRules: [
                (v) => !!v || "Merci de saisir un bref descriptif",
                (v) =>
                (v && v.length <= 100) || "Merci de saisir moin de 100 charactères",
            ],
            usersRules: [
                (v) => !!v || "Merci de choisir un ou plusieurs utilisateurs",
                (v) => v.length > 0 || "Merci de vérifier le nombre selectionné",
            ],
            folder: null,
            level: null,
            folders: ["Item 1", "Item 2", "Item 3", "Item 4"],
        };
    },
    created() {
        this.initialize();
    },
    methods: {
        async initialize() {
            this.folders = this.listFolders;

            if (this.taskDate != null) {
                this.titleTask = this.taskDate.title;
                this.users = [];
                this.taskDate.Users.forEach((element) => {
                    this.users.push({
                        _id: element.user._id,
                        lastName: element.user.lastName,
                    });
                });
                this.folder = this.folders.filter((folder) => {
                    return folder.id.includes(this.taskDate.folder);
                })[0];
                this.level = this.taskDate.level;
            }
        },
        async validate() {
            if (this.$refs.form.validate()) {
                this.overlay = true;
                var task = {
                    addFolder: this.addFolder,
                    title: this.titleTask,
                    level: this.level._id,
                    users: this.users,
                };

                if (!this.addFolder) {
                    task.folder = this.folder.id;
                } else {
                    task.folder = this.labelFolder;
                }

                if (this.taskDate == null) {
                    var res = await taskApi.store(task);

                    res.data.newFolder = this.addFolder;
                    this.$emit("newTask", res.data);
                    this.closeAndReset();
                } else {
                    task.id = this.taskDate.id;
                    var DataIsModify = false;

                    if (task.title != this.taskDate.title) {
                        DataIsModify = true;
                    }
                    if (task.level != this.taskDate.level._id) {
                        DataIsModify = true;
                    }
                    if (this.checkUserIfModify()) {
                        DataIsModify = true;
                    }

                    if (DataIsModify) {
                        var res = await taskApi.update(task);
                        this.$emit("modifyTask", res.data);
                        this.dialog = false;
                    } else {
                        this.closeAndReset();
                    }
                }
                this.overlay = false;
            }
        },
        checkUserIfModify() {
            var tabTest = [];
            this.taskDate.Users.forEach((element) => {
                tabTest.push({
                    _id: element.user._id,
                    lastName: element.user.lastName,
                });
            });

            if (JSON.stringify(tabTest) === JSON.stringify(this.users)) {
                return false;
            }

            return true;
        },
        closeAndReset() {
            if (!this.taskDate) {
                this.$refs.form.reset();
            } else {
                this.initialize();
            }
            this.dialog = false;
            this.addFolder = false;
        },
    },
};