<template >
  <v-container style="background: #04153b" fill-height fluid>
    <v-row justify="center">
      <v-col class="col-12" align="center">
        <v-img src="../assets/logo.png" height="200px" width="200"></v-img>
      </v-col>
      <v-col
        style="border-bottom: 8px solid #a09f9f"
        xl="4"
        md="4"
        sm="2"
        cols="1"
      >
      </v-col>
      <v-col align="center" style="padding: 0" xl="4" md="4" sm="8" cols="10">
        <v-card color="#a09f9f" style="border-radius: 50px 0px">
          <v-card-title
            class="white--text text-h4 justify-center"
            width="100px"
          >
            Créer un Mot de passe :
          </v-card-title>
          <v-form
            class="white--text"
            ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-card-text>
              <v-text-field
                v-model="password"
                :append-icon="show4 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passwordRules"
                solo
                :type="show4 ? 'text' : 'password'"
                label="password"
                @click:append="show4 = !show4"
                required
              ></v-text-field>
              <v-text-field
                :append-icon="show4 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="ConfirmPasswordRules"
                solo
                :type="show4 ? 'text' : 'password'"
                label="Confirmation password"
                @click:append="show4 = !show4"
                required
              ></v-text-field>
            </v-card-text>

            <v-card-actions class="justify-center">
              <v-btn
                :disabled="!valid"
                @click="validate"
                color="primary"
                width="200px"
              >
                Valider
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-card>
      </v-col>
      <v-col
        xl="4"
        md="4"
        sm="2"
        cols="1"
        style="border-top: 8px solid #a09f9f"
      >
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import userApi from "../../service/axios/user";
export default {
  data() {
    return {
      valid: true,
      show4: false,
      ConfirmPasswordRules: [
        (v) => !!v || "Merci de saisir un password",
        (v) => v == this.password || "Merci de saisir le même mot de passe",
      ],
      password: "",
      confirmPassword: "",
      passwordRules: [
        (v) => !!v || "Merci de saisir un password",
        (v) => v.length >= 8 || "Merci de saisir min 8 caractères",
      ],
    };
  },
  created() {},
  methods: {
    async validate() {
      if (this.$refs.form.validate()) {
        var res = await userApi.resetPasswordUser(
          {
            password: this.password,
          },
          this.$route.params.token
        );

        console.log(res);

        this.$refs.form.reset();
        this.$router.push({ name: "Login" });
      }
    },
  },
};
</script>

