import api from "./api";

//collection of request to API for computer
export default {
    index() {
        return api.get("folders");
    },
    listFolder() {
        return api.get("listfolder");
    },
    archived(id) {
        return api.get(`folder/${id}/archived`);
    },
    delete(id) {
        return api.get(`folder/${id}/delete`);
    },
    foldersArchived() {
        return api.get("folders/archived");
    },
};